<template>
  <div class="document-container">
    <h6 class="document-title">
      {{ questionnaire.name }}
    </h6>

    <div class="" v-if="questionnaire.type === 'only_file'">
      <div class="document-clue" v-if="questionnaire.hint_text">
        {{ questionnaire.hint_text }}
      </div>
      <div class="achievement-list__item--info--block">
        <file-component @change="file => setFiles(file)"></file-component>
      </div>
    </div> 

    <div class="" v-if="questionnaire.type === 'multiple'">
      <div class="document-clue" v-if="questionnaire.hint_text">
        {{ questionnaire.hint_text }}
      </div>
      <UploadQuestionnaireMultiple
      :questionnaire="questionnaire" />
    </div>

    <div class="" v-if="questionnaire.type === 'radio_and_multiple'">
      <div class="document-clue">
        {{ questionnaire.subname }}
      </div>
      <x-input
        class="inline-checkbox"
        type="radio"
        name="yesno"
        :options="yesno"
        error-behavior="submit"
      ></x-input>
      <div class="document-clue" v-if="questionnaire.hint_text">
        {{ questionnaire.hint_text }}
      </div>
      <UploadQuestionnaireMultiple fileMultiple />
    </div>

  </div>
</template>

<script>
import FileComponent from '@/components/Ui/file'
import UploadQuestionnaireMultiple from './UploadQuestionnaireMultiple.vue'
export default {
  components: {
    UploadQuestionnaireMultiple,
    FileComponent,
  },
  props: {
    questionnaire: {
      type: Object,
      required: true,
    },
  },
  data() {
    return {
      form: {},
      yesno: ['Да', 'Нет'],
    }
  },
  methods: {
    setFiles(file) {
      this.form.document = file
    },
    async submit(indicators) {
      await this.saveAchievement({ ...this.form, indicators })
      this.form = {}
    },
  },
}
</script>

