<template>
  <div class="photo-upload-container">
    <a-upload
      name="avatar1"
      list-type="picture-card"
      class="avatar-uploader"
      :show-upload-list="false"
      accept="image/jpeg,image/png,image/heic"
      :before-upload="beforeUpload"
      action="/api/v1/users/me"
      @change="handleChange"
    >
      <img v-if="imageUrl" :src="imageUrl" alt="avatar" />
      <div v-else>
        <v-icon name="camera" />
        <!-- <div class="ant-upload-text">
          Upload
        </div> -->
      </div>
    </a-upload>
    <div class="help-text">Нажмите, чтобы прикрепить (до 2МБ)</div>
  </div>
</template>

<script>
function getBase64(img, callback) {
  const reader = new FileReader()
  reader.addEventListener('load', () => callback(reader.result))
  reader.readAsDataURL(img)
}
export default {
  props: {
    avatarSrc: {
      type: String,
      required: false,
    },
  },
  data() {
    return {
      loading: false,
      imageUrl: '',
      fileList: [],
    }
  },
  created() {
    this.imageUrl = this.avatarSrc
  },
  methods: {
    handleChange(info) {
      if (info.file.status === 'uploading') {
        this.loading = true
        return
      }
      if (info.file.status === 'done') {
        // Get this url from response in real world.
        getBase64(info.file.originFileObj, imageUrl => {
          this.imageUrl = imageUrl
          this.loading = false
        })
      }
    },
    beforeUpload(file) {
      const isJpgOrPng = file.type === 'image/jpeg' || file.type === 'image/png'
      if (!isJpgOrPng) {
        this.$message.error('You can only upload JPG file!')
      }
      const isLt2M = file.size / 1024 / 1024 < 2
      if (!isLt2M) {
        this.$message.error('Image must smaller than 2MB!')
      }
      return isJpgOrPng && isLt2M
    },
    async onSuccess(res, file) {
      const data = new FormData()
      data.append('image', file.raw)
      data.append('format', file.raw.type.split('/')[1])
      try {
        const response = await this.$store.dispatch('saveEntity', {
          entityName: '/profile/avatar',
          method: 'post',
          data,
        })
        this.avatar = response.avatar
      } catch (e) {
        console.log(e)
      } finally {
        this.loading = false
      }
    },
    onProgress(file, fileList) {
      this.loading = true
    },
  },
}
</script>
