<template>
  <li class="achievement-list__item">
    <div class="achievement-list__item--info">
      <div class="achievement-list__item--info--block">
        <span class="count">{{ position }}</span>
        <x-input
        v-for="input in questionnaire.inputs"
        :key="input.name"
        :label="!getVisible(input.visible) ? '' : getLabel(input)"
        :type="!getVisible(input.visible) ? 'hidden' : input.type"
        :name="input.name"
        :validation="!getVisible(input.visible) ? '' : input.validation"
        :options="getOptions(input)"
        :class="input.class">
        </x-input>
      </div>
      <!-- <div class="achievement-list__item--info--block">
        <file-component
          v-if="indicator.document"
          @change="file => setFiles(file)"
        >
        </file-component>
      </div> -->
      <a-button
        type="info"
        block
        html-type="submit"
        class="document-container-button"
      >
        <v-icon name="plus"></v-icon>
        <span>Добавить</span>
      </a-button>
    </div>
  </li>
</template>

<script>
import FileComponent from '@/components/Ui/file'
export default {
  components: {
    FileComponent,
  },
  props: {
    position: {
      type: Number,
      required: true,
    },
    setFiles: {
      type: Function,
      required: false,
    },
    questionnaire: {
      type: Object,
       required: true
    }
  },
  computed: {
    getPeriod() {
      const { start_achievement, finish_achievement } = this.indicator.period
      const years = []
      for (let i = start_achievement; i <= finish_achievement; i++) {
        years.push(i)
      }
      return years
    },
  },
  methods: {
    getVisible(cb) {
      return cb ? cb() : true
    },
    getOptions(input) {
      return input.optionName ? this.$store.getters[input.optionName] : typeof input.options == 'function' ? input.options() : input.options
    },
    getLabel(input) {
      return typeof input.label == 'function' ? input.label() : input.label
    }
  }
}
</script>

<style lang="scss" scoped>
.count {
  color: #d1d1d1;
  font-weight: 500;
  font-size: 20px;
}
</style>
