<template>
  <div class="file-container">
    <div class="file-container__files">
      <div
        class="file-container__files--file"
        v-for="file in files"
        :key="file.name"
      >
        <v-icon name="file"></v-icon>
        <span>{{ file.name }}</span>
        <v-icon @click="removeFiles(file)" class="remove" name="times"></v-icon>
      </div>
    </div>
    <FormulateInput
      class="document-uploader user-upload not-active achievement"
      label-class="document-label"
      name="document"
      type="file"
      :validation="`mime:${acceptFormat.join(',')}|download`"
      :validation-rules="{
        download: ({ value }) => {
          return files.length
        },
      }"
      :validation-messages="{
        download: 'Загрузите документ',
      }"
    >
      <template #element>
        <input
          ref="files"
          @dragover="dragHover = true"
          @dragleave="dragHover = false"
          @dragend="dragHover = false"
          @drop="setFile"
          @change="setFile"
          type="file"
          :multiple="false"
          id="upload"
          :accept="acceptFormat.join(',')"
          :isDrag="dragHover"
          class="document-uploader--input"
        />
        <label name="file" for="upload" class="document-uploader--custom">
          <v-icon name="file"></v-icon>
          <span>{{ getLabel }}</span>
        </label>
      </template>
    </FormulateInput>
  </div>
</template>

<script>
export default {
  props: {
    multiple: {
      type: Boolean,
    },
  },
  data() {
    return {
      dragHover: false,
      uploadPercentage: 0,
      files: [],
      acceptFormat: ['application/pdf', 'application/msword', 'application/vnd.openxmlformats-officedocument.wordprocessingml.document']
    }
  },
  computed: {
    getLabel() {
      return this.dragHover ? 'Перетащите сюда' : 'Прикрепить файл'
    },
  },
  methods: {
    removeFiles(file) {
      this.files.splice(this.files.findIndex(i => i.name === file.name), 1)
    },
    async setFile(event) {
      this.dragHover = false;
      if (this.multiple) {
        event.target.files.forEach((file, index) => {
          this.files.find(outerFile => {
            console.log(outerFile, file)
            return outerFile.name === file.name
          })
          if (this.files.find(outerFile => outerFile.name === file.name)) {
            return
          }
          if (!this.acceptFormat.includes(file.type)) {
            this.$toast.error('Неккоректный формат')
            return
          }
          this.files.push(file)
        })
        this.$emit('change', this.files)
      } else {
        const files = event.target.files
        if (!files.length) return
        console.log(files[0])
        if (!this.acceptFormat.includes(files[0].type)) {
          // this.$toast.error('Неккоректный формат')
          return
        }
        this.files = [event.target.files[0]]
        this.$refs.files.value = null
        console.log(this.files)
        this.$emit('change', this.files[0])
      }
    },
  },
}
</script>

